import { useContext } from 'react'
import { VStack, HStack, Heading, Button, Divider, TabList, Tabs, Tab, Text, Circle, useDisclosure } from '@chakra-ui/react'
import { ChatIcon } from '@chakra-ui/icons'
import { BeatLoader } from 'react-spinners';
import { FriendContext } from './Home'
import AddFriend from './AddFriend';
import { LayoutGroupContext } from 'framer-motion';
import { useNavigate } from 'react-router';
import LogoutButton from './LogoutButton';

const Sidebar = () => {
  const {friendList, setFriendList} = useContext(FriendContext);
  const { isOpen, onOpen, onClose } = useDisclosure();
  const navigate = useNavigate();

  return (
    <>
        <VStack py="1rem">
            <Tabs variant='solid-rounded'>
                <VStack as={TabList}>
                    {friendList.map((friend) => (
                        <HStack as={Tab}>
                            <Text>{friend.username}</Text>
                            <Circle bg={friend.connected ? "green" : "red"} w="10px" h="10px"/>
                        </HStack>
                    ))}
                </VStack>
            </Tabs>
            <VStack position="fixed" bottom={10}>
                <Divider m={5}/>
                <Button onClick={onOpen}>Add Friend &nbsp;<ChatIcon /></Button>
                <LogoutButton />
            </VStack>
            
        </VStack>
        <AddFriend isOpen={isOpen} onClose={onClose}/>
    </>
  )
}

export default Sidebar;